$primary-color : #6071cd;
$sub-color : #ff3a96;
$green-color : #208b73;
$red-color : #d1452e;
$gray-color : #111111;
$gray02-color:#222222;
$gray03-color:#333333;
$gray04-color:#444444;
$gray06-color:#666666;
$gray09-color:#999999;


/////////////////////////////////////////////////////////////////////////  LNB
.lnb {
    width: 220px;
    display: inline-block;
    .lnb-menu {
        background: #485cc7;
        border-radius: 10px;
        >li {
            width: 100%;
            border-bottom: 1px solid #374cb7;
            color: #fff;
            >a {
                display: block;
                font-size: 14px;
                color: #ffffff;
                padding: 15px 20px;
                &.active {
                    color: #b14248;
                }
            }
            &.active {
                background: #374cb7;
                border-radius: 10px 10px 0 0;
            }
            &:last-child {
                border-radius: 0 0 10px 10px;
            }
            .lnb-depth03 {
                display: none;
                padding: 0 10px;

                li {
                    margin-bottom: 15px;

                    a {
                        display: block;
                        position: relative;
                        padding: 0 5px;
                        font-size: 13px;
                        color: #666666;

                        &.active,
                        &:hover {
                            color: #b14248;
                            font-weight:700;
                        }
                    }
                }
            }

            &.active {
                >a {
                    color: #ffb709;
                    font-weight: bold;
                    /* padding: 0 10px; */
                    border: none;
                    background: url("../images/lnb_plus.png") no-repeat right center;
                }
            }
            &.sub {
                border:none;
                &.mt50 {
                    margin-top:30px;
                }
                a {
                    display:table;
                    width:100%;
                    margin:10px 0;
                    padding:10px 20px;
                    font-size:14px;
                    color:#505985;
                    border:1px solid #eaeaea;
                    border-radius: 10px;
                    box-sizing: border-box;
                    div {
                        &:first-child {
                            display:table-cell;
                            width:30px;
                            vertical-align: middle;
                        }
                        img {
                            width:16px;
                            height:16px;
                            vertical-align: top;
                            margin-top: 3px;
                        }
                    }
                }
            }
            > img {
                margin-top:40px;
            }
        }
    }
}

///////////////////////////////////////////////////////////////////////// 마이페이지 레이아웃
.mypage {
    width: calc(100% - 290px);
    margin-left: 50px;

}

///////////////////////////////////////////////////////////////////////// 시험 팝업
.exam_wrap {
    padding:30px 35px;
    border-radius: 10px;
    background:#f3f3f3;
}
.exam_pop_top {
    margin-bottom:30px;
    > .pop_tit {
        font-size:26px;
        font-weight:700;
        color:$gray04-color;
        margin-top:8px;
        img {
            margin-right:10px;
            vertical-align: inherit;
        }
    }
}
.exam_pop_con {
    max-height:540px;
    overflow-y: scroll;
    padding:40px;
    border-radius: 10px;
    background:#fff;
    > .pop_con {
        border:1px solid #eaeaea;
        border-radius: 10px;
        .pop_con_top {
            display:flex;
            padding:40px 50px;
            border-radius: 10px 10px 0 0;
            background:#f9fafe;
            .que_num {
                display: inline-table;
                width: 40px;
                height: 40px;
                margin-right: 15px;
                font-size: 15px;
                color: #fff;
                text-align: center;
                border-radius: 30px;
                background: $primary-color;
                span {
                    font-size: 15px;
                    display: inline-block;
                    padding-top: 8px;
                }
            }
            .que_tit {
                > h3 {
                    font-size:18px;
                    color:$gray-color;
                }
                > p {
                    font-size:14px;
                    color:$primary-color;
                }
                > span {
                    display:inline-block;
                    margin-top:25px;
                    font-size:14px;
                    color:$gray06-color;
                }
                > .ex_box {
                    width:550px;
                    margin-top:30px;
                    padding:15px 0;
                    text-align:center;
                    background:#eaecf4;
                    > p {
                        font-size:14px;
                        color:$gray03-color;
                        strong {
                            font-weight:700;
                            color:$sub-color;
                        }
                    }
                }
            }
        }
        .pop_con_btm{
            padding:40px 100px;
            background:#fff;
            > p {
                font-size:20px;
                color:$gray03-color;
                margin-bottom:20px;
                > span {
                    font-size:15px;
                    color:$primary-color;
                }
                > b {
                    display:inline-block;
                    min-width:100px;
                    margin:0 10px;
                    border-bottom:1px solid #000;
                }
            }
            .que_con {
                p {
                    margin-left:10px;
                    font-size:15px;
                    vertical-align: middle;
                }
                textarea {
                    width:100%;
                    padding:30px;
                    font-size:14px;
                    color:$gray09-color;
                    border:1px solid #eaeaea;
                    resize: none;
                    box-sizing: border-box;
                }
                &.list {
                    label {
                        display:block;
                        margin-bottom: 20px;
                    }
                }
            }
            .que_con_btm {
                margin-top:10px;
                button {
                    width:100px;
                    padding:5px 0;
                    font-size:16px;
                    border-radius: 5px;
                }
            }
        }
    }
}
.exam_pop_btm  {
    margin-top:30px;
    > button {
        font-size:16px;
        padding:10px 30px;
        border-radius: 5px;
        box-shadow: 0px 4px 5px -1px #6f6f6f33;
        &:first-child {
            img {
                margin-right:15px;
            }
        }
        &:last-child {
            img {
                margin-left:15px;
            }
        }
    }
}

///////////////////////////////////////////////////////////////////////// 그림판 
.canvas {width: 800px;height: 450px;}
.controls .controls__colors {display: flex;}
#jsErase {vertical-align: super;}
.color_wrap {padding:10px 40px;border-radius: 30px;background:#fff;min-height: 35px;vertical-align: top;}
.jscolor {vertical-align: top;margin-top:3px;}
.controls__colors {display:inline-block;}
.controls__colors .controls__color {display:inline-block;text-align:left;width: 25px;height: 25px;border-radius: 25px;margin-right: 5px;cursor: pointer;vertical-align: sub;}
.jscolor {color: transparent;font-size: 0;line-height: 0;vertical-align: super;width: 25px;height: 25px;border-radius: 25px;border-width: 0px;margin-right: 5px;cursor: pointer;border:1px solid #ddd;}
.controls {display: flex;flex-direction: column;align-items: center;}
.controls .controls__btns {margin-top: 20px;margin-bottom: 30px;}
.controls__btns button:active {transform: scale(0.98);}
.controls__btns button {all: unset;cursor: pointer;background-color: white;padding: 5px 0px;width: 80px;text-align: center;border-radius: 10px;box-shadow: 3px 3px 3px grey;border: 2px solid rgba(0, 0, 0, 0.2);color: rgba(0, 0, 0, 0.8);text-transform: uppercase;font-weight: 600;font-size: 12px;}
.controls .controls__range {margin-top: 20px;}
.controls__range {display:inline-block;}
.controls__range input {margin-top:10px;}